@import "src/common/styles/scss/variables";

/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}

/*
  2. Remove default margin
*/
* {
  margin: 0;
}

/*
  3. Allow percentage-based heights in the application
*/
html, body {
  height: 100%;
  width: 100%;
}

/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  min-width: min-content;
  /*background-color: #10121B;*/
  font-family: $fontFamily;
}

/*
  6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/*
  7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}

/*
  8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/*
  9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}


.wrapper {
  position: relative;
  z-index: 1;
  background-color: $mainBgcColor;
  transition: all .4s ease-out;
  color: #a4acc4;

  &.light {
    background-color: $lightMainBgcColor;
    transition: all .4s ease-out;
    color: $lightTextColor;
  }

  .bgcBlur {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: all .4s ease-out;
    backdrop-filter: blur(3px);
  }

  .bglines {
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    position: absolute;
    top: 0;
    z-index: -1;

    &.light {
      span {
        background-color: rgba(203, 206, 216, .5);;
      }
    }

    span {
      background: rgba(46, 52, 78, .3);
      display: inline-block;
      width: 1px;
      height: 100%;
      min-height: 100vh;
      position: absolute;
      top: 0;

      &:first-child {
        left: 20%;
      }

      &:nth-child(2) {
        left: 40%;
      }

      &:nth-child(3) {
        left: 60%;
      }

      &:nth-child(4) {
        left: 80%;
      }

      &:nth-child(5) {
        left: 0;
      }
    }
  }
}














